.input-group input {
    border: none;
    border-bottom: 1px solid #000;
    outline: none;
    padding-bottom: 10px;
    border-radius: 0;
    padding-right: 40px;
    font-size: 18px;
}

.search-content-container {
    display: flex;
    align-items: center;
}

.input-group {
    border: none; /* Remove default input group border */
   
}

.input-group .btn.clear-button {
    border: none;
    background: none;
    padding: 0;
    margin: 0;
    position: absolute;
    right: 4px;
    top: 10px;
    z-index: 6;
}
.input-group input:focus {
    outline: none;
    box-shadow: none;
    background: none;
    border-bottom-color: #000;
  }
  input::placeholder {
    font-size: 17px;
}